import axios from 'axios'
export default {
	data() {
		return {
			personal: []
		}
	},
	methods: {
		bjgg() {
			this.ggbj = Array.from(this.$refs.bjgg.files).map(f => {
				return window.URL.createObjectURL(f)
			})
			// console.log(this.ggbj)
			this.$refs.photo.src = this.ggbj;
			let file = this.$refs.bjgg.files[0]
			//				console.log(file)
			/* eslint-disable no-undef */
			let param = new FormData() // 创建form对象
			param.append('user_name', "17301794508")
			//				param.append('mFile', 'file')
			param.append('mFile', file, file.name) // 通过append向form对象添加数据
			console.log("222222");
			//				console.log(param.get('file')) // FormData私有类对象，访问不到，可以通过get判断值是否传进去

			let config = {
				headers: {
					'Content-Type': 'multipart/form-data'
				}
			}
			// 添加请求头
			axios.post('http://121.42.177.97:8080/red/use/hard_pic.do?', param, config)
				.then(response => {
					console.log("222222");
					//						console.log(response.data)
				})
				.catch((error) =>
					console.log(error)
				);
		}
	},
	mounted() {
		//获得个人昵称
		axios.post('http://121.42.177.97:8080/red/user/findfriend.do?name=17301794508')
			.then((response) => {
				//									console.log(response.data.data)
				this.personal = response.data.data
			})
			.catch(function(error) {
				console.log(error);
			});
	}
}



